import { OutboundLink } from "gatsby-plugin-google-gtag";
import React from "react";

const ExternalLink = ({ href, children }: { href: string, children: React.ReactNode; }) => {
    return (
      <a className="text-blue-600 underline" href={href}>
        {children}
      </a>
    );
  };

  const TrackedExternalLink = ({ href, children }: { href: string, children: React.ReactNode; }) => {
    return (
      <OutboundLink className="text-blue-600 underline" href={href}>
        {children}
      </OutboundLink>
    );
  };

export { ExternalLink, TrackedExternalLink };